import React, { useContext } from "react";
import { Card } from "react-bootstrap";
import { StudentNoContext } from "../../contexts/StudentNo";

import { BlockMath } from "react-katex";
export default function Item5() {
  const studentNoState = useContext(StudentNoContext).studentNoState;
  const lower =
    studentNoState.studentNo !== ""
      ? parseFloat("9." + studentNoState.studentNo.substring(4, 5))
      : "9.ùõº";
  const higher =
    studentNoState.studentNo !== ""
      ? parseFloat(
          parseFloat("9." + studentNoState.studentNo.substring(4, 5)) + 0.2
        ).toFixed(1)
      : "9.ùõº + 0.2";
  return (
    <Card>
      <Card.Header>
        #5<BlockMath math={`\\int_{${lower}}^{${higher}} f(x) dx`}></BlockMath>
      </Card.Header>

      <Card.Body>
        <Card.Title>Rectangle Rule (left)</Card.Title>
        <Card.Title>Trapezoidal</Card.Title>
        <Card.Title>Errors</Card.Title>
      </Card.Body>
    </Card>
  );
}
